@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;600&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Inter', sans-serif;
}

body {
  margin: 0;
  background-color: #ffffff;
  font-size: 16px;
}

html, body, #root {
  height: 100%;
}

a {
  color: inherit;
  text-decoration: none;
  display: block;
}

p {
  margin: 0;
}

ul, li {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.text-uppercase {
  text-transform: uppercase;
  color: darkred;
}

@media screen and (min-width: 2000px) {
  body {
    font-size: 30px;
  }
}

@media screen and (min-width: 1000px) {
  body {
    font-size: 17px;
  }
}

@media screen and (max-width: 600px) {
  body {
    font-size: 11px;
  }
}

@media screen and (max-width: 350px) {
  body {
    font-size: 10px;
  }
}


.game-container {
    display: -webkit-flex;
    display: flex;
    height: 100%;
}

.game-container.loading {
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.quiz-area {
    background: #F5F5F7;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    padding: 0 8.8em 0 5em;
    width: 73%;
}

.quiz-question {
    max-width: 60%;
}

.quiz-question p {
    font-size: 2em;
    line-height: 116%;
    font-weight: 600;
}

.progress-area {
    width: 27%;
}

.progress-area {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.mobile-menu {
    display: none;
    -webkit-align-self: flex-end;
            align-self: flex-end;
}

img[class^='mobile-logo-'] {
    width: 2em;
    height: 1.75em;
}

.loading-description {
    font-size: 1.5em;
    line-height: 116%;
    font-weight: 600;
}

.loading-spinner {
    width: 4em;
    height: 4em;
    border-radius: 50%;
    border: 3px solid #D0D0D8;
    border-top-color: #FF8B37;
    -webkit-animation: spinner 0.7s linear infinite;
            animation: spinner 0.7s linear infinite;
    display: block;
    margin-bottom: 1.5em;
}

@-webkit-keyframes spinner {
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes spinner {
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}


/* MEDIA QUERIES */
@media screen and (max-width: 1500px) {
    .quiz-question {
        max-width: 100%;
    }
}

@media screen and (max-width: 1300px) {
    .quiz-area {
        padding: 0 6.6em 0 3.75em;
        width: 70%;
    }
    .progress-area {
        width: 30%;
    }
}

@media screen and (max-width: 1000px) {
    .mobile-menu {
        display: block;
    }
    .progress-area {
        display: none;
    }
    .quiz-area {
        width: 100%;
        padding: 2em 2.5em 3.5em;
        -webkit-justify-content: space-between;
                justify-content: space-between;
    }
    .quiz-question p {
        text-align: center;
    }
}

@media screen and (max-width: 350px) {
    .quiz-question p {
        font-size: 1.8em;
    }
}

.quiz-answers {
    display: grid;
    grid-template-columns: repeat(2, auto);
    grid-gap: 2em;
    gap: 2em;
    counter-reset: count;
}

@media screen and (max-width: 768px) {
    .quiz-answers {
        margin: 0 -1.5em;
    }
}

@media screen and (max-width: 425px) {
    .quiz-answers {
        grid-template-columns: repeat(1, auto);
    }
}

.polygon-item {
    position: relative;
}

.polygon-item:before,
.polygon-item:after{
    content: '';
    background: #D0D0D8;
    width: 1em;
    height: 1px;
    position: absolute;
    top: calc(50% - 1px/2);
}

.polygon-item:before {
    left: -1em;
}

.polygon-item:after {
    right: -1em;
}

.polygon-item span {
    display: block;
    position: relative;
    font-weight: 400;
    font-size: 1.25em;
    line-height: 116%;
    color: #000000;
}

.hex {
    position: relative;
    height: 4.5em;
    min-width: 100px;
    font-weight: bold;
    text-align: center;
    background: #D0D0D8;
    -webkit-clip-path: polygon(1.13em 0px, calc(100% - 1.13em) 0px, 100% 50%, calc(100% - 1.13em) 100%, 1.13em 100%, 0px 50%);
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    padding-left: 2em;
    color: #fff;
}

.hex:before {
    position: absolute;
    content: '';
    height: calc(100% - 2px);
    width: calc(100% - 2px);
    left: 1px;
    top: 1px;
    -webkit-clip-path: polygon(1.13em 0px, calc(100% - 1.13em) 0px, 100% 50%, calc(100% - 1.13em) 100%, 1.13em 100%, 0px 50%);
    z-index: -1;
}
.hex:before {
    background: #ffffff;
}

/* specific styles for QUIZ ANSWERS */
.polygon-item.polygon-item-answer {
    counter-increment: count;
    cursor: pointer;
}
.polygon-item.polygon-item-answer span:before {
    content: counter(count, upper-alpha);
    color: #FF8B37;
    font-weight: 600;
    margin-right: 8px;
}

/* specific styles for polygon items from SCORE area */
.polygon-item.polygon-item-amount {
    width: calc(100% - 8.5em);
    margin-bottom: 0.5em;
}

.polygon-item.polygon-item-amount:before,
.polygon-item.polygon-item-amount:after {
    width: 4.25em;
}

.polygon-item.polygon-item-amount:before {
    left: -4.25em;
}

.polygon-item.polygon-item-amount:after {
    right: -4.25em;
}

.polygon-item-amount span {
    display: block;
    position: relative;
    font-weight: 400;
    font-size: 1.25em;
    line-height: 116%;
    color: #000000;
    text-align: center;
}

.polygon-item-amount .hex {
    padding-left: 0;
    -webkit-justify-content: center;
            justify-content: center;
    height: 2.5em;
}

/* Hover --- only for QUIZ-ANSWERS */
.polygon-item.polygon-item-answer:hover:before,
.polygon-item.polygon-item-answer:hover:after{
    background: #FF8B37;
}
.polygon-item.polygon-item-answer:hover .hex{
    background: #FF8B37;
}

/* Correct --- only for QUIZ-ANSWERS */
.polygon-item.polygon-item-answer.correct:before,
.polygon-item.polygon-item-answer.correct:after{
    background: #47D867;
}
.polygon-item.polygon-item-answer.correct .hex{
    background: #47D867;
}
.polygon-item.polygon-item-answer.correct .hex:before {
    background: #E6FAEA;
}

/* Wrong --- only for QUIZ-ANSWERS */
.polygon-item.polygon-item-answer.wrong:before,
.polygon-item.polygon-item-answer.wrong:after{
    background: #EC6259;
}
.polygon-item.polygon-item-answer.wrong .hex{
    background: #EC6259;
}
.polygon-item.polygon-item-answer.wrong .hex:before {
    background: #FDEEED;
}

/* Selected --- only for QUIZ-ANSWERS */
.polygon-item.polygon-item-answer.selected:before,
.polygon-item.polygon-item-answer.selected:after{
    background: #FF8B37;
}
.polygon-item.polygon-item-answer.selected .hex{
    background: #FF8B37;
}
.polygon-item.polygon-item-answer.selected .hex:before {
    background: #FFF3EB;
}

/* WINNING SCORE'S STATUSES */
/* Current win amount */
.polygon-item.winned span{
    color: #FF8B37;
}
.polygon-item.winned:before,
.polygon-item.winned:after{
    background: #FF8B37;
}
.polygon-item.winned .hex{
    background: #FF8B37;
}

/* Previous win amount */
.polygon-item.prev-winned span{
    color: #D0D0D8;
}
.polygon-item.prev-winned:before,
.polygon-item.prev-winned:after{
    background: #D0D0D8;
}
.polygon-item.prev-winned .hex{
    background: #D0D0D8;
}

/* MEDIA QUERIES */
@media screen and (max-width: 1300px) {
    .polygon-item.polygon-item-amount {
        width: calc(100% - 8em);
    }
    .polygon-item.polygon-item-amount:before,
    .polygon-item.polygon-item-amount:after {
        width: 4em;
    }
    .polygon-item.polygon-item-amount:before {
        left: -4em;
    }
    .polygon-item.polygon-item-amount:after {
        right: -4em;
    }
}

@media screen and (max-width: 1000px) {
    .polygon-item.polygon-item-amount {
        margin-bottom: 1em;
    }
    .polygon-item.polygon-item-amount:last-child {
        margin-bottom: 0;
    }
}

@media screen and (max-width: 768px) {
    .polygon-item.polygon-item-amount .hex {
        height: 32px;
    }
}

@media screen and (max-width: 425px) {
    .polygon-item-answer .hex {
        height: 5em;
    }
    .polygon-item.polygon-item-answer:before,
    .polygon-item.polygon-item-answer:after {
        width: 1.5em;
    }
    .polygon-item.polygon-item-answer:before {
        left: -1.5em;
    }
    .polygon-item.polygon-item-answer:after {
        right: -1.5em;
    }
}

@media screen and (max-width: 350px) {
    .polygon-item span {
        font-size: 1.4em;
    }
    .polygon-item.polygon-item-amount {
        margin-bottom: 0.8em;
    }
    .polygon-item.polygon-item-answer .hex {
        height: 5.6em;
    }
    .polygon-item.polygon-item-answer:before,
    .polygon-item.polygon-item-answer:after {
        width: 1.7em;
    }
    .polygon-item.polygon-item-answer:before {
        left: -1.7em;
    }
    .polygon-item.polygon-item-answer:after {
        right: -1.7em;
    }
}

